.tooltip-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.35);
    padding: 10px;
}

.tooltip-box {
    padding: 15px;
    background-color: #fff;
    color: #000;
    font: 13px/1.25 Arial, Helvetica, sans-serif;
    border: 1px solid #eee;
    border-radius: 5px;
}

.tooltip-box:after {
    content: '';
    height: 10px;
    width: 10px;
    background-color: #fff;
    position: absolute;
    transform: rotate(45deg);
}

.tooltip-box.top:after {
    left: 50%;
    bottom: -6px;
    margin-left: -5px;
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
}

.tooltip-box.bottom:after {
    left: 50%;
    top: -6px;
    margin-left: -5px;
    border-left: 1px solid #eee;
    border-top: 1px solid #eee;
}

.tooltip-box button {
    border-radius: 4px;
    border: 0;
    color: #fff;
    cursor: pointer;
    line-height: 30px;
    min-width: 80px;
    padding: 0 10px;
    margin-right: 10px;
    background-color: #1890ff;
}

.tooltip-box .tooltip-skip {
    background-color: #ff4d4f;
}

.tooltip-title {
    font-size: 15px;
    margin: 0 0 5px;
    font-weight: bold;
}

.tooltip-message {
    margin: 0 0 10px;
}